import React, { useState } from 'react'
import '../css/main/Main.css'
import { HeadProvider, Title, Meta, Link as Links  } from 'react-head'
import { Container, Row, Col } from 'reactstrap'
import { ComponyData } from './data/ComponyData'
import { Link } from 'react-router-dom'


// –®–∞–±–ª–æ–Ω Main: –ê–≤—Ç–æ—Ä–∏–∑–∞—Ü–∏—è, –†–µ–≥–∏—Å—Ç—Ä–∞—Ü–∏—è, –°–±—Ä–æ—Å –ø–∞—Ä–æ–ª—è

export function MainLayout({ children, title = '', desc = '', xs = '', sm = '', md = '', lg = '' })
{

    const color = '#111'
    const data = ComponyData()
    const [lang, setLang] = useState('ru')

    return (
        <>
            <HeadProvider>
                <Title>{title}</Title>
                <Links rel="icon" href="/favicon.ico" />
                <Meta name="author" content="Mobius ltd." />
                <Meta name="description" content={desc} />
                <Meta name="theme-color" content={color} />
                <Meta name="msapplication-navbutton-color" content={color} />
                <Meta name="apple-mobile-web-app-status-bar-style" content={color} />
            </HeadProvider>
            <div id="default" style={{background: 'url(/img/bg.jpg) no-repeat #000'}}>
                <Container>
                    <Row>
                        <Col xs={xs} sm={sm} md={md} lg={lg}>
                            <header>
                                <Link to="/">
                                    <img src="/logo.svg" alt="Mobius Trade" />
                                </Link>
                                <h4 className="mb-4">{desc}</h4>
                            </header>
                            {children}
                            <div className="d-flex justify-content-center mt-3">
                                <button type="button" className="btn text-white" onClick={() => setLang('ru')}>
                                    <img src="/lang/ru.svg" alt="ru" style={{width: '30px'}} />
                                </button>
                                <button type="button" className="btn text-white" onClick={() => setLang('en')}>
                                    <img src="/lang/en.svg" alt="en" style={{width: '30px'}} />
                                </button>
                            </div>                        
                            <footer>
                                <p className="m-0 text-muted">
                                    &copy; {data[0].name}, {new Date().getFullYear()} 
                                    &#160;&middot;&#160;
                                    <small style={{fontSize: '10px'}}>All Right Recerved</small>
                                </p>
                            </footer>
                        </Col>
                    </Row>
                </Container>                  
            </div>
          
        </>
    )
}