import React, { useState, useEffect } from 'react'
import { useForm } from 'react-hook-form'
import { FaEye, FaTelegramPlane } from 'react-icons/fa'
import { Card, CardBody, CardFooter, CardHeader, Col, FormFeedback, FormGroup, InputGroup, InputGroupAddon, InputGroupText, Row } from 'reactstrap'
import { MainLayout } from './components/MainLayout'
import { useI18n } from '@shopify/react-i18n'
import { Link, useHistory } from 'react-router-dom'
import Loading from './components/plugin/Loading'

import en from './translations/en.json'
import ru from './translations/ru.json'
// import Login from './login'


export default function Signup()
{

    const history = useHistory()
    const Title = 'MOBIUSTRADER 7'
    const Description = '–§–æ—Ä–º–∞ —Ä–µ–≥–∏—Å—Ç—Ä–∞—Ü–∏–∏'

    const [types, setTypes] = useState(true)
    const [logined, setLogin] = useState(null)
    const [loading, setLoading] = useState(false)
    const [email, setEmail] = useState('')

    const changeType = () => setTypes(prev => !prev)

    const { register, errors, watch, handleSubmit } = useForm({
        criteriaMode: "all"
    })

    const username  = watch("name")
    const loginMail = watch("login")

    const onSubmit = data => {
        //alert(JSON.stringify(data))

        setLogin(data)
        
        if(data) {
            setLoading(true) 
            setTimeout(() => {
                history.push('/offer')
            }, 2000)            
        }
    } 

    const firstLetterUpper = iText => {
        if (!iText) { return true }
        return iText[0].toUpperCase() === iText[0]
    }

    const isValid = em => {
        const emailFind = 'test@mail.com'
        //validUser.map(i => i.email)
        if(em === emailFind) {
            return false
        } else if (em === '') { return true }
        return true
    }

    const [i18n] = useI18n({
        id: 'NotFound',
        fallback: ru,
        translations(locale) {
            if (locale === 'ru') {
                return ru
            } else if (locale === 'en') {
                return en
            }
        }
    })

    useEffect(() => {
        if(logined) {
            window.sessionStorage.setItem('user', JSON.stringify(logined)) 
        }
    }, [email, logined])




    function changeEmailValue(e) 
    {
        setEmail(e.target.value)
        window.localStorage.setItem('email', e.target.value)
    }

    return (
        <MainLayout title={Description} desc={Title} xs={{size: 12}} sm={{size: 8, offset: 2}} md={{size: 6, offset: 3}} lg={{size: 4, offset: 4}}>
            <Card className="shadow">
                <CardHeader className="p-0">
                    <Row className="m-0">
                        <Col xs={6} className="p-0">
                            <Link to="/" className="bg-dark btn btn-block">–ê–≤—Ç–æ—Ä–∏–∑–∞—Ü–∏—è</Link>
                        </Col>
                        <Col xs={6} className="p-0">
                            <Link to="/signup" className="btn btn-block">–†–µ–≥–∏—Å—Ç—Ä–∞—Ü–∏—è</Link>
                        </Col>
                    </Row>
                </CardHeader>
                <CardBody>
                    <form onSubmit={handleSubmit(onSubmit)}>
                        <FormGroup>
                            <input
                                type="text" 
                                name="name" 
                                placeholder="–í–∞—à–µ –∏–º—è" 
                                autoComplete="off"
                                className={
                                    errors.name ? 
                                        "is-invalid form-control rounded-0 form-custom-input" : 
                                        "form-control rounded-0 form-custom-input"
                                }
                                defaultValue={username}
                                ref={register({
                                    required: true, 
                                    maxLength: 30, 
                                    validate: { firstLetterUpper }
                                })}
                            />
                            {
                                errors.name &&
                                <>
                                    {errors?.name?.types?.required && <FormFeedback>First name is required</FormFeedback>}
                                    {errors?.name?.types?.maxLength && <FormFeedback>Max length of first name is 30 characters!</FormFeedback>}
                                    {errors?.name?.types?.firstLetterUpper && <FormFeedback>First letter should be uppercased!</FormFeedback>}                  
                                </>
                            }
                        </FormGroup>
                        <FormGroup>
                            <input 
                                type="text" 
                                name="login" 
                                placeholder="Email" 
                                defaultValue={loginMail}
                                autoComplete="off"
                                className={
                                    errors.login ? 
                                        "is-invalid form-control rounded-0 form-custom-input" : 
                                        "form-control rounded-0 form-custom-input"
                                }
                                ref={
                                    register(
                                    {
                                        required: true, 
                                        pattern: /^((([0-9A-Za-z]{1}[-0-9A-z\.]{1,}[0-9A-Za-z]{1})|([0-9–ê-–Ø–∞-—è]{1}[-0-9–ê-—è\.]{1,}[0-9–ê-–Ø–∞-—è]{1}))@([-0-9A-Za-z]{1,}\.){1,2}[-A-Za-z]{2,})$/u,
                                        validate: {isValid}
                                    })
                                } 
                                onChange={changeEmailValue}
                            />
                            {
                            errors.login &&
                            <>
                                {errors?.login?.types?.required && <FormFeedback>Email is required</FormFeedback>}
                                {errors?.login?.types?.pattern && <FormFeedback>This address is not an email</FormFeedback>}
                                {errors?.login?.types?.isValid && <FormFeedback>There is no such user, or the user is not registered</FormFeedback>}                  
                            </>
                            }
                        </FormGroup>
                        <FormGroup>
                            <InputGroup>
                                <input 
                                    type={types ? "password" : "text"}
                                    name="password" 
                                    autoComplete="off"
                                    className={
                                        errors.password ? 
                                            "is-invalid form-control rounded-0 form-custom-input" : 
                                            "form-control rounded-0 form-custom-input"
                                    }
                                    placeholder="Password" 
                                    ref={
                                        register({ 
                                            required: true
                                        })
                                    }
                                />                   
                                <InputGroupAddon addonType="prepend" onClick={changeType}>
                                    <InputGroupText className={errors.password ? "cp no-valid" : "cp"}>
                                        <FaEye />
                                    </InputGroupText>
                                </InputGroupAddon>
                                {
                                    errors.password &&
                                    <>
                                        {errors?.password?.types?.required && <FormFeedback>Password is required</FormFeedback>}                   
                                    </>
                                }
                            </InputGroup>
                        </FormGroup>
                        
                        <button type="submit" className="btn btn-success btn-block rounded-0 mt-3 font-weight-bold">
                            {loading ? <Loading color="#78dac3" classes="text-white" /> : <><FaTelegramPlane />&#160;&#160;–ó–∞—Ä–µ–≥–∏—Å—Ç—Ä–∏—Ä–æ–≤–∞—Ç—å—Å—è</>}
                        </button>
                    </form>
                </CardBody>
                <CardFooter>
                    <button className="btn btn-sm btn-outline-light btn-block pt-2 pb-2">
                        <img src="/img/google_icon.svg" alt="" style={{width: '20px'}} />
                        &#160;&#160;&#160;Log in with Google
                    </button>
                    <div className="or">OR</div>
                    <button className="btn btn-sm btn-dark btn-block pt-2 pb-2 mb-4">
                        <img src="/img/apple_icon.svg" alt="" style={{width: '20px'}} />
                        &#160;&#160;&#160;Log in with Apple ID
                    </button>
                </CardFooter>
            </Card>

        </MainLayout>
    )
}