import React, { useState } from 'react'
import { Link } from 'react-router-dom'
import { Row, Col, Card, CardHeader, CardBody, Table } from 'reactstrap'
import { AdminLayout } from '../components/AdminLayout'
import { Tag } from '../components/helper/CustomTag'
import { Scrollbars } from 'react-custom-scrollbars'


export default function Deposit() 
{
    const Title = 'Deposit'
    const [type, setType] = useState('Webmoney')

    return (
        <AdminLayout title={Title}>
            <Row className="justify-content-center">
                <Col xs="12">
                    <Row className="align-items-center">
                        <Col md="4" className="mb-4">
                            <Tag tag="h2" className="h3 mb-0 page-title">
                                <i class="fe fe-24 fe-plus-circle text-primary"></i> {Title}
                            </Tag>
                        </Col>
                        <Col md="6" className="text-left mb-4">
                            <Link to="/balance">
                                <u className="text-white">Deposit and Withdrawal History</u>
                            </Link>
                        </Col>
                        <Col md="2" className="mb-4">
                            <select 
                                name="ant-select-dropdown-menu" 
                                className="form-control bg-dark text-white border border-secondary" 
                                defaultValue="USD"
                            >
                                <option value="BTC">BTC</option>
                                <option value="EUR">EUR</option>
                                <option value="USD">USD</option>
                                <option value="CHF">CHF</option>
                                <option value="BCH">BCH</option>
                                <option value="RUB">RUB</option>
                            </select>
                        </Col>
                    </Row>
                    <Row>
                        <Col md="4">
                            <Card className="shadow border-0">
                                <Scrollbars 
                                    autoHide
                                    autoHideTimeout={1000}
                                    autoHideDuration={200}
                                    style={{ width: '100%', height: 363 }}
                                >
                                    <CardBody className="p-0">
                                        <ul class="list-group cp">
                                            <li class="list-group-item d-flex" onClick={() => setType('Webmoney')}>
                                                <img src="/img/webmoney_icon.svg" alt="" style={{width: '32px'}} />
                                                <strong class="m-0 pt-1 ml-2">Webmoney</strong>
                                            </li>
                                            <li class="list-group-item d-flex" onClick={() => setType('Bitcoin')}>
                                                <img src="/img/bitcoin.svg" alt="" style={{width: '32px'}} />
                                                <strong class="m-0 pt-1 ml-2">Bitcoin</strong>
                                            </li>
                                            <li class="list-group-item d-flex" onClick={() => setType('Bitcoin Cash')}>
                                                <img src="/img/bitcoin_cash.svg" alt="" style={{width: '32px'}} />
                                                <strong class="m-0 pt-1 ml-2">Bitcoin Cash</strong>
                                            </li>
                                            <li class="list-group-item d-flex" onClick={() => setType('Litecoin')}>
                                                <img src="/img/litecoin.svg" alt="" style={{width: '32px'}} />
                                                <strong class="m-0 pt-1 ml-2">Litecoin</strong>
                                            </li>
                                            <li class="list-group-item d-flex" onClick={() => setType('QIWI')}>
                                                <img src="/img/qiwi.svg" alt="" style={{width: '32px'}} />
                                                <strong class="m-0 pt-1 ml-2">QIWI</strong>
                                            </li>
                                            <li class="list-group-item d-flex" onClick={() => setType('DASH')}>
                                                <img src="/img/dash-crypto.svg" alt="" style={{width: '32px'}} />
                                                <strong class="m-0 pt-1 ml-2">DASH</strong>
                                            </li>
                                            <li class="list-group-item d-flex" onClick={() => setType('Yandex.Money')}>
                                                <img src="/img/yandex-money.svg" alt="" style={{width: '32px'}} />
                                                <strong class="m-0 pt-1 ml-2">Yandex.Money</strong>
                                            </li>
                                            <li class="list-group-item d-flex" onClick={() => setType('PrivatBank')}>
                                                <img src="/img/privat_bank.svg" alt="" style={{width: '32px'}} />
                                                <strong class="m-0 pt-1 ml-2">PrivatBank</strong>
                                            </li>
                                        </ul>
                                    </CardBody>
                                </Scrollbars>
                            </Card>
                        </Col>
                        <Col md="8">
                            <Card className="shadow">
                                <CardBody>
                                    <div className="clearfix">
                                        <span className="float-right">
                                            <span className="badge badge-primary mr-2 font-weight-light" style={{fontSize: '15px'}}>
                                                <strong className="font-weight-bold">0.8%</strong> &#160;commission
                                            </span>
                                            <span className="badge badge-success font-weight-light" style={{fontSize: '15px'}}>
                                                <strong className="font-weight-bold">500%</strong> &#160;bonus
                                            </span>
                                        </span>
                                    </div>
                                    <Tag tag="h3" className="mt-5 mb-4">Deposit via {type}</Tag>
                                    <Tag tag="p" className="text-white">The minimum deposit is <strong>‚Ç¨0.01</strong></Tag>
                                    <Row className="mt-4">
                                        <Col md="4">
                                            <label className="text-muted">Amount to deposit:</label>
                                            <input 
                                                type="number"
                                                class="form-control bg-dark text-white border border-secondary" 
                                                autoComplete="off" 
                                                min="0" 
                                                name="Amount" 
                                                defaultValue="1000" 
                                            />
                                        </Col>
                                        <Col md="4">
                                            <label className="text-muted">Currency:</label>
                                            <select 
                                                name="ant-select-menu" 
                                                className="form-control bg-dark text-white border border-secondary" 
                                                defaultValue="WME"
                                            >
                                                <option value="WME">WME</option>
                                                <option value="WMR">WMR</option>
                                                <option value="WMU">WMU</option>
                                                <option value="WMZ">WMZ</option>
                                            </select>
                                        </Col>

                                        <Col md="4">
                                            <label className="text-muted">Bonus:</label>
                                            <select 
                                                name="ant-menu" 
                                                className="form-control bg-dark text-white border border-secondary" 
                                                defaultValue="500%"
                                            >
                                                <option value="0%">0%</option>
                                                <option value="50%">50%</option>
                                                <option value="100%">100%</option>
                                                <option value="200%">200%</option>
                                                <option value="500%">500%</option>
                                            </select>
                                        </Col>
                                        <Col md="4" className="mt-5 mb-3">
                                            <button className="btn btn-lg btn-success btn-block">Proceed</button>
                                        </Col>
                                    </Row>
                                </CardBody>
                            </Card> 
                        </Col>
                    </Row>
                </Col>
            </Row>
        </AdminLayout>
    )
}