import React, { useState } from 'react'
import { Dropdown, DropdownToggle, DropdownMenu, DropdownItem } from 'reactstrap'
import { Link, useHistory } from 'react-router-dom'
import { FaPowerOff } from 'react-icons/fa'


export function WrapperMenu()
{

    const history = useHistory()
    const [dropdownOpen, setDropdownOpen] = useState(false)
    const toggle = () => setDropdownOpen(prevState => !prevState)

    function destroy() 
    {
        window.sessionStorage.removeItem('access')
        setTimeout(() => {
            history.push('/')
        }, 1000)  
    }

    return (
        <Dropdown tag="li" isOpen={dropdownOpen} toggle={toggle}>
            <DropdownToggle tag="a" className="nav-link dropdown-toggle text-muted pr-0 cp">
                <span className="avatar avatar-sm mt-2">
                    <img src="/assets/avatars/face-1.jpg" alt="..." className="avatar-img rounded-circle" />
                </span>
            </DropdownToggle>
            <DropdownMenu right>
                <Link to="/profile" className="dropdown-item">
                    <span class="fe fe-24 fe-user fe-12 pr-2 text-muted"></span> Profile
                </Link>
                <Link to="/settings/profile" className="dropdown-item">
                    <span class="fe fe-24 fe-settings fe-12 pr-2 text-muted"></span> Settings
                </Link>
                <Link to="/activities" className="dropdown-item">
                    <span class="fe fe-24 fe-message-square fe-12 pr-2 text-muted"></span> Activities
                </Link>
                <DropdownItem divider />
                <div onClick={() => destroy()} className="dropdown-item cp">
                    <FaPowerOff className="pr-2 text-danger" style={{fontSize: '19px', marginTop: '-2px'}} /> Sign Out
                </div>
            </DropdownMenu>
        </Dropdown>
    )
}