import React, { useState } from 'react'
import { AdminLayout } from '../components/AdminLayout'
import { Tag } from '../components/helper/CustomTag'
import { Col, Row, Table } from 'reactstrap'
import DatePicker from 'react-datepicker'
import { AiOutlineCalendar } from 'react-icons/ai'
import 'react-datepicker/dist/react-datepicker.css'


export default function Balance()
{
    const Title = 'Deposit and Withdrawal History'

    let d = new Date()
    let from = d.setMonth(d.getMonth() - 1)
    const [firststartDate, setFirstStartDate] = useState(from)
    const [startDate, setStartDate] = useState(new Date())
    const Status = (data) => {
        return data === 1 ? 
            <span className="badge badge-pill badge-success font-weight-bold btn-block">Paid up</span> : 
            <span className="badge badge-pill badge-warning font-weight-bold btn-block">Waiting for payment</span>
    }
    const Balances = [
        {
            date: 'November 21, 2020, 07:12 PM',
            account: 'EUR (1197153)',
            amount: '‚Ç¨ 255.00',
            method: 'MasterCard',
            status: 0
        },
        {
            date: 'November 21, 2020, 05:15 AM',
            account: 'RUB (1230917)',
            amount: '‚ÇΩ 50.00',
            method: 'Visa',
            status: 0
        },
        {
            date: 'November 21, 2020, 05:15 AM',
            account: 'USD (1199740)',
            amount: '$ 50.00',
            method: 'Skrill',
            status: 1
        }
    ]


    return (
        <AdminLayout title={Title}>
            <Row className="justify-content-center">
                <Col md="4">
                    <Tag tag="h2" className="h3 mb-0 page-title mb-4">
                        {Title}
                    </Tag>
                </Col>
                <Col md="8">
                    <Row>
                        <Col md={9}>
                            <Row>
                                <Col md={6}>
                                    <div className="input-group mb-3" style={{width: '215px'}}>
                                        <div className="input-group-prepend border border-right-0 border-secondary">
                                            <span className="input-group-text border-0">
                                                <i className="fe fe-calendar"></i>
                                            </span>
                                        </div>
                                        <DatePicker 
                                            selected={firststartDate} 
                                            onChange={date => setFirstStartDate(date)} 
                                            dateFormat="MM dd, yyyy"
                                            className="form-control text-white bg-dark border border-secondary border-left-0"
                                        />                            
                                    </div>
                                </Col>
                                <Col md={6}>
                                    <div className="input-group mb-3" style={{width: '215px'}}>
                                        <div className="input-group-prepend border border-right-0 border-secondary">
                                            <span className="input-group-text border-0">
                                                <i className="fe fe-calendar"></i>
                                            </span>
                                        </div>
                                        <DatePicker 
                                            selected={startDate} 
                                            onChange={date => setStartDate(date)} 
                                            dateFormat="MM dd, yyyy"
                                            className="form-control text-white bg-dark border border-secondary border-left-0"
                                        />                            
                                    </div>
                                </Col>
                            </Row>
                        </Col>
                        <Col md={3} className="text-lg-right text-md-right text-center mb-4">
                            <div className="btn-group btn-group-toggle btn-group-sm" data-toggle="buttons">
                                <label className="btn btn-primary active">
                                    <input type="radio" name="options" autocomplete="off" checked /> Deposit
                                </label>
                                <label className="btn btn-primary">
                                    <input type="radio" name="options" autocomplete="off" /> Withdrawal
                                </label>
                            </div>
                        </Col>
                    </Row>
                </Col>
            </Row>
            <Row>
                <Col xs={12}>
                    <div className="table-responsive">
                        <Table className="table-hover table-borderless table-striped bg-white mb-5">
                            <thead>
                                <tr>
                                    <th className="font-weight-bold">#</th>
                                    <th className="font-weight-bold">Date</th>
                                    <th className="font-weight-bold">Account</th>
                                    <th className="font-weight-bold">Amount</th>
                                    <th className="font-weight-bold">Method</th>
                                    <th className="font-weight-bold">Status:</th>
                                </tr>
                            </thead>
                            <tbody>
                                {Balances.map((b, idx) => (
                                <tr key={idx}>
                                    <td>{idx+1}</td>
                                    <td>
                                        <AiOutlineCalendar className="mr-2" /> {b.date}
                                    </td>
                                    <td>{b.account}</td>
                                    <td>
                                        <h5 className="m-0">{b.amount}</h5>
                                    </td>
                                    <td>
                                        <img 
                                            src={`/img/${b.method}.ico`} 
                                            alt={b.method} 
                                            className="mr-2" 
                                            style={{width:'40px'}} 
                                        /> 
                                        {b.method}
                                    </td>
                                    <td>
                                        {Status(b.status)}
                                    </td>
                                </tr>                                
                                ))}
                            </tbody>
                        </Table>                        
                    </div>

                </Col>
            </Row>
        </AdminLayout>
    )
}