import React, { useState } from 'react'
import { MainLayout } from './components/MainLayout'
import { useHistory } from 'react-router-dom'
import { Card, CardHeader, CardBody, FormGroup, FormFeedback, Alert } from 'reactstrap'
import { useForm } from 'react-hook-form'
import { FaCheckSquare } from 'react-icons/fa'
import { Tag } from './components/helper/CustomTag'
import Loading from './components/plugin/Loading'


export default function Reset()
{
    const Title = 'MOBIUSTRADER 7'
    const Description = '–°–±—Ä–æ—Å –ø–∞—Ä–æ–ª—è'
    const history = useHistory()

    const [login, setLogin] = useState(null)
    const [loading, setLoading] = useState(false)
    const [visible, setVisible] = useState(false)

    const { register, errors, watch, handleSubmit } = useForm({
        criteriaMode: "all"
    })
    const onDismiss = () => {return history.push('/')}

    const isValid = em => {
        const emailFind = 'test@mail.com'
        if(em === emailFind) {
          return true
        } else if (em === '') { return true }
        return false
    }

    const onSubmit = data => {

        setLogin(data)
        
        if(data) {
            setLoading(true) 
            setTimeout(() => {
                setVisible(true) 
            }, 1500)  
        }

    } 
    const mail = watch("email")

    return (
        <MainLayout title={Description} desc={Title} xs={{size: 12}} sm={{size: 8, offset: 2}} md={{size: 6, offset: 3}} lg={{size: 4, offset: 4}}>
            {
                visible ? 
                <Alert color="success" isOpen={true} toggle={onDismiss}>
                    <FaCheckSquare /> <b>–£—Å–ø–µ—à–Ω–æ</b>&#160; –ù–∞ –≤–∞—à e-mail: {login ? login.email : ''} –≤—ã—Å–ª–∞–Ω—ã –∏–Ω—Å—Ç—Ä—É–∫—Ü–∏–∏ –ø–æ —Å–±—Ä–æ—Å—É –ø–∞—Ä–æ–ª—è, 
                    –∑–∞–∫—Ä–æ–π—Ç–µ –¥–∞–Ω–Ω–æ–µ —Å–æ–æ–±—â–µ–Ω–∏–µ –¥–ª—è –≤—Ö–æ–¥–∞.
                </Alert> :
                <Card className="shadow">
                    <CardHeader>
                        <div className="d-flex justify-content-center">
                            <Tag tag="h6" className="m-0">{Description}</Tag>
                        </div>
                    </CardHeader>
                    <CardBody>
                        <div className="d-flex justify-content-center">
                            <Tag tag="small">–í–≤–µ–¥–∏—Ç–µ E-mail —É–∫–∞–∑–∞–Ω–Ω—ã–π –ø—Ä–∏ —Ä–µ–≥–∏—Å—Ç—Ä–∞—Ü–∏–∏.</Tag>
                        </div>
                        <form onSubmit={handleSubmit(onSubmit)}>
                            <FormGroup>
                            <input 
                                    type="text" 
                                    name="email" 
                                    placeholder="–í–≤–µ–¥–∏—Ç–µ –≤–∞—à Email" 
                                    autoComplete="off"
                                    defaultValue={mail}
                                    className={
                                        errors.email ? 
                                            "is-invalid form-control rounded-0 form-custom-input" : 
                                            "form-control rounded-0 form-custom-input"
                                    }
                                    ref={
                                        register({
                                            required: true, 
                                            pattern: /^((([0-9A-Za-z]{1}[-0-9A-z\.]{1,}[0-9A-Za-z]{1})|([0-9–ê-–Ø–∞-—è]{1}[-0-9–ê-—è\.]{1,}[0-9–ê-–Ø–∞-—è]{1}))@([-0-9A-Za-z]{1,}\.){1,2}[-A-Za-z]{2,})$/u,
                                            validate: {isValid}
                                        })
                                    } 
                                />
                                {
                                    errors.email &&
                                    <>
                                        {errors?.email?.types?.required && <FormFeedback>–í—ã –Ω–µ —É–∫–∞–∑–∞–ª–∏ Email –∞–¥—Ä–µ—Å.</FormFeedback>}
                                        {errors?.email?.types?.pattern && <FormFeedback>–≠—Ç–æ—Ç –∞–¥—Ä–µ—Å –Ω–µ —è–≤–ª—è–µ—Ç—Å—è —ç–ª–µ–∫—Ç—Ä–æ–Ω–Ω–æ–π –ø–æ—á—Ç–æ–π.</FormFeedback>}
                                        {errors?.email?.types?.isValid && <FormFeedback>–¢–∞–∫–æ–≥–æ –ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª—è –Ω–µ—Ç –∏–ª–∏ –ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª—å –Ω–µ –∑–∞—Ä–µ–≥–∏—Å—Ç—Ä–∏—Ä–æ–≤–∞–Ω.</FormFeedback>}                  
                                    </>
                                }
                            </FormGroup>
                            <FormGroup>
                                <button type="submit" className="btn btn-success btn-block rounded-0 mt-3 font-weight-bold">
                                    {loading ? <Loading color="#78dac3" classes="text-white" /> : '–°–±—Ä–æ—Å–∏—Ç—å –ø–∞—Ä–æ–ª—å'}
                                </button>
                            </FormGroup>
                        </form>
                    </CardBody>
                </Card>
            }
        </MainLayout>
    )
}