import React, { useState, useEffect } from 'react'
import { Link } from 'react-router-dom'
import { Tag } from '../helper/CustomTag'
import { AsideNavBar } from './nav/AsideNavBar'
import { Scrollbars } from 'react-custom-scrollbars'

export function Aside(props)
{
    const [isOpen, setIsOpen] = useState(true)
    const toggleNavbar = () => setIsOpen(!isOpen)

    useEffect(() => {
        if(isOpen) {
            document.getElementById('theme').classList.remove('collapsed')
        } else {
            document.getElementById('theme').classList.add('collapsed')
        }
    }, [isOpen])

    return (
        <Tag 
            tag="aside" 
            id="leftSidebar" 
            className="sidebar-left border-right bg-white shadow"
            data-simplebar="init"
        >
            {/*
                <Nav className="ml-auto" navbar>
                    {menu.map(m => (
                        <li className="nav-item" key={m.id}>
                            <Link to={m.href} className={activeClassHref(`${m.href}`)}>{m.text}</Link>
                        </li>
                    ))}
                </Nav>                        
            */}
            <div className="simplebar-wrapper" style={{margin: '0px'}}>
                <div className="simplebar-height-auto-observer-wrapper">
                    <div className="simplebar-height-auto-observer"></div>
                </div>
            </div>
            <div className="simplebar-mask">
                <div className="simplebar-offset" style={{right: '0px', bottom: '0px'}}>
                    <div className="simplebar-content-wrapper" style={{height: '100%', overflow: 'hidden'}}>
                        <div className="simplebar-content p-0">
                            <a 
                                className="btn collapseSidebar toggle-btn d-lg-none text-muted ml-2 mt-3 cp" 
                                onClick={toggleNavbar}
                            >
                                <i className="fe fe-x"></i>
                            </a>
                            <Scrollbars 
                                autoHide
                                autoHideTimeout={1000}
                                autoHideDuration={200}
                                style={{ width: '100%', height: 620 }}
                            >
                            <Tag 
                                tag="nav" 
                                className="vertnav navbar navbar-light p-0"                                
                            >
                                <div className="w-100 mb-4 d-flex">
                                    <Link to="/dashboard" className="navbar-brand mx-auto mt-2 flex-fill text-center">
                                        <img src="/logo.svg" style={{width: '32px'}} alt="Mobius Trade" />
                                    </Link>
                                </div>
                                <AsideNavBar open={props.open} />
                            </Tag>
                            </Scrollbars>
                        </div>
                    </div>
                </div>
            </div>
        </Tag>
    )
}