import React from 'react'
import { Link } from 'react-router-dom'
import { Col, Row } from 'reactstrap'
import { AdminLayout } from '../components/AdminLayout'
import { Tag } from '../components/helper/CustomTag'

export default function Withdraw()
{

    const Title = '–°–Ω—è—Ç—å —Å—Ä–µ–¥—Å—Ç–≤–∞'

    return (
        <AdminLayout title={Title}>
            <Row className="justify-content-center">
                <Col xs="12">
                    <Row className="align-items-center">
                        <Col md="4">
                            <Tag tag="h2" className="h3 mb-0 page-title mb-4">
                                <i class="fe fe-24 fe-dollar-sign text-primary"></i> {Title}
                            </Tag>
                        </Col>
                        <Col md="6" className="text-left mb-4">
                            <Link to="/balance"><u>Deposit and Withdrawal History</u></Link>
                        </Col>
                        <Col md="2" className="mb-4">
                            <select 
                                name="ant-select-dropdown-menu" 
                                className="form-control bg-dark text-white border border-secondary" 
                                defaultValue="USD"
                            >
                                <option value="BTC">BTC</option>
                                <option value="EUR">EUR</option>
                                <option value="USD">USD</option>
                                <option value="CHF">CHF</option>
                                <option value="BCH">BCH</option>
                                <option value="RUB">RUB</option>
                            </select>
                        </Col>
                    </Row>
                </Col>
            </Row>
        </AdminLayout>
    )
}