import React, { useState } from 'react'
import { AdminLayout } from '../components/AdminLayout'
import { SecuritySettings } from '../components/grid/SecuritySettings'
import { ProfileSettings } from '../components/grid/ProfileSettings'
import { NotificationsSettings } from '../components/grid/NotificationsSettings'
import { Link, useParams, useLocation } from 'react-router-dom'
import { Col, Row } from 'reactstrap'
import { Tag } from '../components/helper/CustomTag'


export default function Settings()
{
    const Title = 'Settings'
    const {tab} = useParams()
    const location = useLocation()

    const ActiveTab = (href) => {return href === location.pathname ? 'nav-link active' : 'nav-link'}
    const Items = [
        {name: 'Profile', link: '/settings/profile'},
        {name: 'Security', link: '/settings/security'},
        {name: 'Notifications', link: '/settings/notifications'}
    ]
    const [currencyPage, setCurrencyPage] = useState(tab)

    return (
        <AdminLayout title={Title}>
            <Row className="justify-content-center">
                <Col xs={12} lg={10} xl={8} className="mx-auto">
                <Tag tag="h2" className="h3 mb-4 page-title">Settings {tab}</Tag>
                    <div className="my-4">
                        <ul className="nav nav-tabs mb-4" id="myTab" role="tablist">
                            {Items.map((h, idx) => (
                            <li key={idx} className="nav-item">
                                <Link class={ActiveTab(`${h.link}`)} onClick={() => setCurrencyPage(`${h.link.substr(10)}`)}  to={h.link}>
                                    {h.name}
                                </Link>
                            </li>
                            ))}
                        </ul>
                        {currencyPage === 'security' && <SecuritySettings />}
                        {currencyPage === 'profile' && <ProfileSettings />}
                        {currencyPage === 'notifications' && <NotificationsSettings />}
                    </div> 
                </Col>
            </Row>
        </AdminLayout>
    )
}