import React, { useState, useEffect } from 'react'
import { Alert } from 'reactstrap'
import { MainLayout } from '../components/MainLayout'

export default function Offer()
{
    
    const [email, setEmail] = useState(null)
    const [user, setUser] = useState({})
    const Title = `${user.name}, –ø–æ–∑–¥—Ä–∞–≤–ª—è–µ–º –í–∞—Å —Å —É—Å–ø–µ—à–Ω–æ–π —Ä–µ–≥–∏—Å—Ç—Ä–∞—Ü–∏–µ–π`
    const Description = `–ú—ã –≤—ã—Å–ª–∞–ª–∏ –≤–∞–º —Å—Å—ã–ª–∫—É, –ø–æ –∞–¥—Ä–µ—Å—É ${email} –Ω–∞ –∞–∫—Ç–∏–≤–∞—Ü–∏—é –≤–∞—à–µ–≥–æ –∞–∫–∫–∞—É–Ω—Ç–∞`
 
    useEffect(() => {
        setEmail(window.localStorage.getItem('email'))
        setUser(JSON.parse(window.sessionStorage.getItem('user')))
    }, [])

    return (
        <MainLayout title={Title} desc='MOBIUSTRADER 7' xs={{size: 12}} sm={{size: 8, offset: 2}} md={{size: 6, offset: 3}} lg={{size: 4, offset: 4}}>
            <Alert color="success" className="shadow text-center">
                <h5 className="text-light mt-3"><strong>{Title}</strong></h5>
                <p>{Description}</p>
            </Alert>
        </MainLayout>
    )
}