import React, { useState } from 'react'
import { HeadProvider, Title, Meta, Link as Links } from 'react-head'
//import { I18nContext } from '@shopify/react-i18n'
import { useLocation } from 'react-router-dom'
import { Tag } from './helper/CustomTag'
import { Aside } from './plug/Aside'
import { Wrapper } from './plug/Wrapper'


export function AdminLayout({children, title = 'Mobius Trade', description = '', color = '#222222', isOpen = true})
{

    const location = useLocation()
    const [collapsed, setCollapsed] = useState(isOpen)
    const [fullscheen, setFullscheen] = useState(false)

    const toggleNavbar = () => setCollapsed(!collapsed)
    const pathnameClass = (path) => {
        if(path === '/dashboard') {
            return `main-content p-0${fullscheen === true ? ' m-0' : ''}`
        } else {
            return `main-content${fullscheen === true ? ' m-0' : ''}`
        }
    }

    const elem = document.documentElement

    function openFullscreen() {
        setFullscheen(true)
        if (elem.requestFullscreen) {
            elem.requestFullscreen();
        } else if (elem.webkitRequestFullscreen) { /* Safari */
            elem.webkitRequestFullscreen();
        } else if (elem.msRequestFullscreen) { /* IE11 */
            elem.msRequestFullscreen();
        }
    }

    function closeFullscreen() {
        setFullscheen(false)
        if (document.exitFullscreen) {
            document.exitFullscreen();
        } else if (document.webkitExitFullscreen) { /* Safari */
            document.webkitExitFullscreen();
        } else if (document.msExitFullscreen) { /* IE11 */
            document.msExitFullscreen();
        }
    }

    //useEffect(() => {
    //    document.body.classList.add('vertical', 'dark')
    //}, [])

    return (
        <>
            <HeadProvider>
                <Title>{title}</Title>
                <Links rel="icon" href="/favicon.ico" />
                <Links rel="apple-touch-icon" href="/logo192.png"/>
                <Links rel="manifest" href="/manifest.json" />
                <Meta property="og:type" content="website" />
                <Meta property="og:site_name" content="Mobius Trade" />
                <Meta property="og:image" content="/logo.png" />
                <Meta property="og:title" content={title} />
                <Meta property="og:url" content={`https://mobius.com${location.pathname}`} />
                <Meta property="og:description" content={description} />
                <Meta name="author" content="Mobius ltd." />
                <Meta name="description" content={description} />
                <Meta name="theme-color" content={color} />
                <Meta name="msapplication-navbutton-color" content={color} />
                <Meta name="apple-mobile-web-app-status-bar-style" content={color} />
            </HeadProvider>
            <div id="theme" className={collapsed ? "wrapper vertical dark" : "wrapper vertical dark collapsed"}>
                <div className="container-fluid">
                    <Wrapper func={{fullscheen, openFullscreen, closeFullscreen}} toggleNavbar={toggleNavbar} />
                    {fullscheen === true ? '' : <Aside open={collapsed} />}
                    <Tag tag="main" role="main" className={pathnameClass(location.pathname)}>
                        {children}
                    </Tag>
                </div>
            </div>            
        </>
    )
}