import React, { useState, useEffect } from 'react'
import { Navbar } from 'reactstrap'
import { WrapperMenu } from './nav/WrapperMenu'


export function Wrapper(props)
{

    const [modal, setModal] = useState(false)
    
    const [currency, set–°urrency] = useState('$')
    const [balance, setBalance] = useState(3500)
    const toggle = () => setModal(!modal)

    const DigitalNum = (d) => {
        return d.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",")
    }

    const –°urrency = (e) => {
        const balance = 3500
        if(currency === '$') {
            const b = balance === 0 ? 0 : balance*73
            set–°urrency('‚ÇΩ')
            setBalance(b) // 73
        } else if (currency === '‚ÇΩ') {
            const b = balance === 0 ? 0 : balance
            set–°urrency('‚Ç¨')
            setBalance(b) // 89
        } else if (currency === '‚Ç¨') {
            const b = balance === 0 ? 0 : balance*89
            set–°urrency('$')
            setBalance(b) // 0
        }
    }


    useEffect(() => {
        if(modal) {
            document.body.classList.add('modal-open')
        } else {
            document.body.classList.remove("modal-open")
        }
    }, [modal])


    return (
        <Navbar className={props.func.fullscheen === true ? "topnav m-0" : "topnav"} light>
            <button onClick={props.toggleNavbar} type="button" className="navbar-toggler text-muted mt-2 p-0 mr-3 collapseSidebar">
                <i className="fe fe-menu navbar-toggler-icon"></i>
            </button>
            <div className="mr-auto" data-test={modal}>
                –ë–∞–ª–∞–Ω—Å:&#160;&#160;
                <strong 
                    className="text-success font-weight-bold cp"
                    onClick={() => –°urrency(currency)}
                >
                    {currency} {DigitalNum(balance)}
                </strong>
            </div>

            <ul className="nav">
                {/*
                    <li className="nav-item">
                        <a className="nav-link text-muted my-2" href="#" id="modeSwitcher" data-mode="dark">
                            <i className="fe fe-sun fe-16"></i>
                        </a>
                    </li>                            
                */}
                <li className="nav-item">
                    {
                        props.func.fullscheen === false ?
                        <>
                            <a className="nav-link text-muted my-2 cp" onClick={props.func.openFullscreen}>
                                <span className="fe fe-maximize-2 fe-16"></span>
                            </a>                        
                        </> : 
                        <>
                            <a className="nav-link text-muted my-2 cp" onClick={props.func.closeFullscreen}>
                                <span className="fe fe-minimize-2 fe-16"></span>
                            </a>                         
                        </>
                    }
                </li>
                <li className="nav-item nav-notif">
                    <a className="nav-link text-muted my-2 cp" onClick={toggle}>
                        <span className="fe fe-bell fe-16"></span>
                        <span className="dot dot-md dot-anim bg-success"></span>
                    </a>
                </li>
                <WrapperMenu />
            </ul>

            <div 
                className={modal ? "modal fade modal-notif modal-slide show" : "modal"} 
                tabIndex="-1" 
                role="dialog"
                style={modal ? {display: 'block', background: 'rgb(0 0 0 / 65%)'} : {}}
            >
                <div className="modal-dialog modal-sm" role="document">
                    <div className="modal-content">
                        <div className="modal-header">
                            <h5 className="modal-title" id="defaultModalLabel">Notifications</h5>
                            <button type="button" className="close" onClick={toggle}>
                                <span aria-hidden="true">√ó</span>
                            </button>
                        </div>
                        <div className="modal-body">
                            <div className="list-group list-group-flush my-n3">
                                <div className="list-group-item bg-transparent">
                                    <div className="row align-items-center">
                                        <div className="col-auto">
                                            <span className="fe fe-box fe-24"></span>
                                        </div>
                                        <div className="col">
                                            <small><strong>Package has uploaded successfull</strong></small>
                                            <div className="my-0 text-muted small">Package is zipped and uploaded</div>
                                            <small className="badge badge-pill badge-light text-muted">1m ago</small>
                                        </div>
                                    </div>
                                </div>
                                <div className="list-group-item bg-transparent">
                                    <div className="row align-items-center">
                                        <div className="col-auto">
                                            <span className="fe fe-download fe-24"></span>
                                        </div>
                                        <div className="col">
                                            <small><strong>Widgets are updated successfull</strong></small>
                                            <div className="my-0 text-muted small">Just create new layout Index, form, table</div>
                                            <small className="badge badge-pill badge-light text-muted">2m ago</small>
                                        </div>
                                    </div>
                                </div>
                                <div className="list-group-item bg-transparent">
                                    <div className="row align-items-center">
                                        <div className="col-auto">
                                            <span className="fe fe-inbox fe-24"></span>
                                        </div>
                                        <div className="col">
                                            <small><strong>Notifications have been sent</strong></small>
                                            <div className="my-0 text-muted small">Fusce dapibus, tellus ac cursus commodo</div>
                                            <small className="badge badge-pill badge-light text-muted">30m ago</small>
                                        </div>
                                    </div> 
                                </div>
                                <div className="list-group-item bg-transparent">
                                    <div className="row align-items-center">
                                        <div className="col-auto">
                                            <span className="fe fe-link fe-24"></span>
                                        </div>
                                        <div className="col">
                                            <small><strong>Link was attached to menu</strong></small>
                                            <div className="my-0 text-muted small">New layout has been attached to the menu</div>
                                            <small className="badge badge-pill badge-light text-muted">1h ago</small>
                                        </div>
                                    </div>
                                </div> 
                            </div> 
                        </div>
                        <div className="modal-footer">
                            <button type="button" className="btn btn-secondary btn-block" onClick={toggle}>
                                Clear All
                            </button>
                        </div>
                    </div>
                </div>
            </div>  
        </Navbar>
    )
}