import React, { useEffect, useState } from 'react'
import { HeadProvider, Title } from 'react-head'
import { Link } from 'react-router-dom'
import { Col, Container, Row } from 'reactstrap'
import '../css/error.css'

export function ErrorLayout({children, title = 'Error 404'})
{
    const [login, setLogin] = useState(false)
    useEffect(() => {
        if(window.sessionStorage.getItem('access') !== null) {
            setLogin(true)
        }
    }, [login])

    return (
        <>
            <HeadProvider>
                <Title>{title}</Title>
            </HeadProvider>
            <div id="error">
                <Container>
                    <Row>
                        <Col md="12" className="mt-5 text-center">
                            {children}
                            {
                                login ?
                                    <Link to="/dashboard" className="btn btn-outline-light">Go Home</Link> :
                                    <Link to="/" className="btn btn-outline-light">Go Home</Link>                                
                            }

                        </Col>
                    </Row>
                </Container>
            </div>
        </>
    )
}