import React from 'react'
import { Col, FormGroup, Row } from 'reactstrap'
import CountryList from '../data/CountryList'

export function ProfileSettings()
{
    const Country = CountryList()

    return (
        <form className="mb-5">
            <div className="row mt-5 align-items-center">
                <div className="col-md-3 text-center mb-5">
                    <div className="avatar avatar-xl">
                        <img src="/assets/avatars/face-1.jpg" alt="..." className="avatar-img rounded-circle"/>
                    </div>
                </div>
                <div className="col">
                    <div className="row align-items-center">
                        <div className="col-md-7">
                            <h4 className="mb-1">Brown, Asher</h4>
                            <p className="small mb-3">
                                <span className="badge badge-dark">New York, USA</span>
                            </p>
                        </div>
                    </div>
                    <div className="row mb-4">
                        <div className="col-md-7">
                            <p className="text-muted"> 
                                Lorem ipsum dolor sit amet, consectetur adipiscing elit. 
                                Mauris blandit nisl ullamcorper, rutrum metus in, congue lectus. 
                                In hac habitasse platea dictumst. Cras urna quam, malesuada vitae risus at, 
                                pretium blandit sapien. 
                            </p>
                        </div>
                        <div className="col">
                            <p className="small mb-0 text-white">Nec Urna Suscipit Ltd</p>
                            <p className="small mb-0 text-muted">P.O. Box 464, 5975 Eget Avenue</p>
                            <p className="small mb-0 text-muted">
                                <a href="tel:+15373151481">+1 (537) 315-1481</a>
                            </p>
                        </div>
                    </div>
                </div>
            </div>

            <hr className="my-4" />

            <div className="form-row">
                <div className="form-group col-md-6">
                    <input 
                        type="text" 
                        className="form-control text-white bg-dark border-secondary"
                        defaultValue="Brown"
                        placeholder="Firstname"
                    />
                </div>
                <div className="form-group col-md-6">
                    <input 
                        type="text"
                        className="form-control text-white bg-dark border-secondary" 
                        defaultValue="Asher"
                        placeholder="Lastname"
                    />
                </div>
            </div>
            <div className="form-group">
                <input 
                    type="email" 
                    className="form-control text-white bg-dark border-secondary"
                    defaultValue="brown@asher.me"
                    placeholder="Email"
                />
            </div>
            <div className="form-group">
                <input 
                    type="text" 
                    className="form-control text-white bg-dark border-secondary" 
                    defaultValue="P.O. Box 464, 5975 Eget Avenue" 
                    placeholder="Address"
                />
            </div>
            <div className="form-row">
                <div className="form-group col-md-6">
                    <input 
                        type="text" 
                        className="form-control text-white bg-dark border-secondary" 
                        defaultValue="Nec Urna Suscipit Ltd" 
                        placeholder="Company"
                    />
                </div>
                <div className="form-group col-md-4">
                    <select defaultValue="" className="form-control text-white bg-dark border-secondary">
                        <option value="">Choose State...</option>
                        {Country.map((list, index) => (
                            <option key={index} value={list.country}>{list.country}</option>
                        ))}
                        
                    </select>
                </div>
                <div className="form-group col-md-2">
                    <input 
                        type="text" 
                        className="form-control text-white bg-dark border-secondary" 
                        defaultValue="98232"
                        placeholder="Zip"
                    />
                </div>
            </div>

            <hr className="my-4"/>

            <Row className="mb-4">
                <Col md={6}>
                    <FormGroup>
                        <input 
                            type="password" 
                            className="form-control text-white bg-dark border-secondary" 
                            placeholder="Old Password"
                        />
                    </FormGroup>
                    <FormGroup>
                        <input 
                            type="password" 
                            className="form-control text-white bg-dark border-secondary" 
                            placeholder="New Password"
                        />
                    </FormGroup>
                    <FormGroup>
                        <input 
                            type="password" 
                            className="form-control text-white bg-dark border-secondary" 
                            placeholder="Confirm Password"
                        />
                    </FormGroup>
                </Col>
                <Col md={6}>
                    <p className="mb-2">Password requirements</p>
                    <p className="small text-muted mb-2"> 
                        To create a new password, you have to 
                        meet all of the following requirements: 
                    </p>

                    <ul className="small text-muted pl-4 mb-0">
                        <li> Minimum 8 character </li>
                        <li>At least one special character</li>
                        <li>At least one number</li>
                        <li>Can‚Äôt be the same as a previous password </li>
                    </ul>
                </Col>
            </Row>
            <Row>
                <Col md={6}>
                    <button type="submit" className="btn btn-success btn-block font-weight-bold">
                        Save Change
                    </button>
                </Col>
            </Row>
        </form>
    )
}
