import React, { useState } from 'react'
import { FaCog, FaPlusCircle, FaSearch, FaTimes } from 'react-icons/fa'
import { TabContent, TabPane, Nav, NavItem, NavLink, CardHeader, CardBody, InputGroupText, InputGroupAddon, InputGroup, Row, Col } from 'reactstrap'
import { Forecast } from './tape/forecast'
import { Idea } from './tape/idea'
import { News } from './tape/news'
import { Puls } from './tape/puls'

export function Tape() 
{

    const [activeTab, setActiveTab] = useState('1')
    const toggle = tab => {
        if(activeTab !== tab) setActiveTab(tab);
    }

    return (
        <>
            <CardHeader className="p-1">
                <div className="d-flex justify-content-between">
                    <div style={{width: '70%'}}>
                        <InputGroup className="rounded">
                            <InputGroupAddon addonType="prepend">
                                <InputGroupText className="border-0 rounded">
                                    <FaPlusCircle />
                                </InputGroupText>
                            </InputGroupAddon>
                            <InputGroupAddon addonType="prepend">
                                <InputGroupText className="border-0">
                                    <FaSearch />
                                </InputGroupText>
                            </InputGroupAddon>
                            <input 
                                type="search" 
                                className="form-control form-control-sm bg-dark text-white border border-dark" 
                                placeholder="–ù–∞–∑–≤–∞–Ω–∏–µ –∏–ª–∏ —Ç–∏–∫–µ—Ä" 
                                defaultValue="–õ–µ–Ω—Ç–∞"
                            />
                        </InputGroup>
                    </div>
                    <div>
                        <div class="btn-group">
                            <button type="button" className="btn btn-sm"><FaCog /></button>
                            <button type="button" className="btn btn-sm"><FaTimes /></button>
                        </div>
                    </div>
                </div>
            </CardHeader>
            <CardBody className="p-0" style={{overflow: 'hidden', minHeight: '150px'}}>
                <Nav tabs className="nav-justified">
                    <NavItem className="cp">
                        <NavLink
                            className={ activeTab === '1' ? 'active m-0' : 'm-0' }
                            onClick={() => { toggle('1'); }}
                        >
                            –ù–æ–≤–æ—Å—Ç–∏
                        </NavLink>
                    </NavItem>
                    <NavItem className="cp">
                        <NavLink
                            className={ activeTab === '2' ? 'active m-0' : 'm-0' }
                            onClick={() => { toggle('2'); }}
                        >
                            –ò–¥–µ–∏
                        </NavLink>
                    </NavItem>
                    <NavItem className="cp">
                        <NavLink
                            className={ activeTab === '3' ? 'active m-0' : 'm-0' }
                            onClick={() => { toggle('3'); }}
                        >
                            –ü—Ä–æ–≥–Ω–æ–∑—ã
                        </NavLink>
                    </NavItem>
                    <NavItem className="cp">
                        <NavLink
                            className={ activeTab === '4' ? 'active m-0' : 'm-0' }
                            onClick={() => { toggle('4'); }}
                        >
                            –ü—É–ª—å—Å
                        </NavLink>
                    </NavItem>
                </Nav>
                <TabContent activeTab={activeTab}>
                    <TabPane tabId="1">
                        <Row>
                            <Col sm="12">
                                <News />
                            </Col>
                        </Row>
                    </TabPane>
                    <TabPane tabId="2">
                        <Row>
                            <Col sm="12">
                                <Idea />
                            </Col>
                        </Row>
                    </TabPane>
                    <TabPane tabId="3">
                        <Row>
                            <Col sm="12">
                                <Forecast />
                            </Col>
                        </Row>
                    </TabPane>
                    <TabPane tabId="4">
                        <Row>
                            <Col sm="12">
                                <Puls />
                            </Col>
                        </Row>
                    </TabPane>
                </TabContent>                    
            </CardBody>
        </>
    )
}