import { Table } from "reactstrap";

export function SecuritySettings()
{
    return (
        <>
            <h5 className="mb-0 mt-5 text-white">Security Settings</h5>
            <p className="text-muted">These settings are helps you keep your account secure.</p>
            <div className="list-group mb-5 shadow">
                <div className="list-group-item">
                    <div className="row align-items-center">
                        <div className="col">
                            <strong className="mb-2">Enable Activity Logs</strong>
                            <p className="text-muted mb-0">Donec id elit non mi porta gravida at eget metus.</p>
                        </div> 
                        <div className="col-auto">
                            <div className="custom-control custom-switch">
                                <input type="checkbox" className="custom-control-input" id="activityLog" checked/>
                                <label className="custom-control-label" for="activityLog"></label>
                            </div>
                        </div> 
                    </div> 
                </div> 
                <div className="list-group-item">
                    <div className="row align-items-center">
                        <div className="col">
                            <strong className="mb-2">2FA Authentication</strong>
                            &#160;&#160; <span className="badge badge-pill badge-success">Enabled</span>
                            <p className="text-muted mb-0">Maecenas sed diam eget risus varius blandit.</p>
                        </div> 
                        <div className="col-auto">
                            <button className="btn btn-primary btn-sm">Disable</button>
                        </div> 
                    </div> 
                </div> 
                <div className="list-group-item">
                    <div className="row align-items-center">
                        <div className="col">
                            <strong className="mb-2">Activate Pin Code</strong>
                            <p className="text-muted mb-0">Donec id elit non mi porta gravida at eget metus.</p>
                        </div> 
                        <div className="col-auto">
                            <div className="custom-control custom-switch">
                                <input type="checkbox" className="custom-control-input" id="pinCode"/>
                                <label className="custom-control-label" for="pinCode"></label>
                            </div>
                        </div> 
                    </div> 
                </div> 
            </div>
            <h5 className="mb-0 text-white">Recent Activity</h5>
            <p className="text-muted">Last activities with your account.</p>
            <Table className="border bg-white">
                <thead>
                    <tr>
                        <th>Device</th>
                        <th>Location</th>
                        <th>IP</th>
                        <th>Time</th>
                        <th></th>
                    </tr>
                </thead>
                <tbody>
                    <tr>
                        <th scope="col"><i className="fe fe-globe fe-12 text-muted mr-2"></i>Chrome - Windows 10</th>
                        <td>Paris, France</td>
                        <td>192.168.1.10</td>
                        <td>Apr 24, 2019</td>
                        <td><a href="#" className="text-muted"><i className="fe fe-x"></i></a></td>
                    </tr>
                    <tr>
                        <th scope="col"><i className="fe fe-smartphone fe-12 text-muted mr-2"></i>App - Mac OS</th>
                        <td>Newyork, USA</td>
                        <td>10.0.0.10</td>
                        <td>Apr 24, 2019</td>
                        <td><a href="#" className="text-muted"><i className="fe fe-x"></i></a></td>
                    </tr>
                    <tr>
                        <th scope="col"><i className="fe fe-globe fe-12 text-muted mr-2"></i>Chrome - iOS</th>
                        <td>London, UK</td>
                        <td>255.255.255.0</td>
                        <td>Apr 24, 2019</td>
                        <td><a href="#" className="text-muted"><i className="fe fe-x"></i></a></td>
                    </tr>
                </tbody>
            </Table>
        </>
    )
}