import React, { useState, useEffect } from 'react'
import { useForm } from 'react-hook-form'
import { FaEye, FaQuestionCircle } from 'react-icons/fa'
import { Card, CardBody, CardHeader, Col, FormFeedback, FormGroup, InputGroup, InputGroupAddon, InputGroupText, Row } from 'reactstrap'
import { MainLayout } from './components/MainLayout'
import { useI18n } from '@shopify/react-i18n'
import { Link, useHistory } from 'react-router-dom'
import Loading from './components/plugin/Loading'

import en from './translations/en.json'
import ru from './translations/ru.json'


export default function Login()
{

    const history = useHistory()
    const Title = 'MOBIUSTRADER 7'
    const Description = '–ê–≤—Ç–æ—Ä–∏–∑–∞—Ü–∏—è'

    const [types, setTypes] = useState(true)
    const [logined, setLogin] = useState(null)
    const [loading, setLoading] = useState(false)
    
    const { register, errors, watch, handleSubmit } = useForm({
        criteriaMode: "all"
    })
    const changeType = () => setTypes(prev => !prev)
    const onSubmit = data => {

        setLogin(data)
        
        if(data) {
            setLoading(true) 
            setTimeout(() => {
                history.push('/dashboard')
            }, 3000)            
        }

    } 
    const mail = watch("login")

    const isValid = em => {
        const emailFind = 'test@mail.com'
        //validUser.map(i => i.email)
        if(em === emailFind) {
          return true
        } else if (em === '') { return true }
        return false
    }

    const isValidPass = pass => {
        const passwordFind = '123456'
        //validUser.map(i => i.password)
        if(pass == passwordFind) {
          return true
        } else if (pass == '') { return true }
        return false
    }

    const [i18n] = useI18n({
        id: 'NotFound',
        fallback: ru,
        translations(locale) {
            if (locale === 'ru') {
                return ru
            } else if (locale === 'en') {
                return en
            }
        }
    })

    useEffect(() => {
        if(logined) {
            window.sessionStorage.setItem('access', mail) 
        }
    }, [logined])


    return (
        <MainLayout title={Description} desc={Title} xs={{size: 12}} sm={{size: 8, offset: 2}} md={{size: 6, offset: 3}} lg={{size: 4, offset: 4}}>
            <Card className="shadow">
                <CardHeader className="p-0">
                    <Row className="m-0">
                        <Col xs={6} className="p-0">
                            <Link to="/" className="btn btn-block">–ê–≤—Ç–æ—Ä–∏–∑–∞—Ü–∏—è</Link>
                        </Col>
                        <Col xs={6} className="p-0">
                            <Link to="/signup" className="bg-dark btn btn-block">–†–µ–≥–∏—Å—Ç—Ä–∞—Ü–∏—è</Link>
                        </Col>
                    </Row>
                </CardHeader>
                <CardBody>
                    <form onSubmit={handleSubmit(onSubmit)}>
                        <FormGroup>
                            <input 
                                type="text" 
                                name="login" 
                                placeholder="Email" 
                                autoComplete="off"
                                defaultValue={mail}
                                className={
                                    errors.login ? 
                                        "is-invalid form-control rounded-0 form-custom-input" : 
                                        "form-control rounded-0 form-custom-input"
                                }
                                ref={
                                    register({
                                        required: true, 
                                        pattern: /^((([0-9A-Za-z]{1}[-0-9A-z\.]{1,}[0-9A-Za-z]{1})|([0-9–ê-–Ø–∞-—è]{1}[-0-9–ê-—è\.]{1,}[0-9–ê-–Ø–∞-—è]{1}))@([-0-9A-Za-z]{1,}\.){1,2}[-A-Za-z]{2,})$/u,
                                        validate: {isValid}
                                    })
                                } 
                            />
                        {
                            errors.login &&
                            <>
                                {errors?.login?.types?.required && <FormFeedback>Email is required</FormFeedback>}
                                {errors?.login?.types?.pattern && <FormFeedback>This address is not an email</FormFeedback>}
                                {errors?.login?.types?.isValid && <FormFeedback>There is no such user, or the user is not registered</FormFeedback>}                  
                            </>
                        }

                        </FormGroup>
                        <FormGroup>
                            <InputGroup>
                                <input 
                                    type={types ? "password" : "text"}
                                    name="password" 
                                    className={
                                        errors.password ? 
                                            "is-invalid form-control rounded-0 form-custom-input" : 
                                            "form-control rounded-0 form-custom-input"
                                    }
                                    placeholder="Password" 
                                    ref={
                                        register({ 
                                            required: true,
                                            validate: {isValidPass}
                                        })
                                    }
                                />                   
                                <InputGroupAddon addonType="prepend" onClick={changeType}>
                                    <InputGroupText className={errors.password ? "cp no-valid" : "cp"}>
                                        <FaEye />
                                    </InputGroupText>
                                </InputGroupAddon>
                                {
                                    errors.password &&
                                    <>
                                        {errors?.password?.types?.required && <FormFeedback>Password is required</FormFeedback>}                   
                                        {errors?.password?.types?.isValidPass && <FormFeedback>Password is not valid!</FormFeedback>}                      
                                    </>
                                }
                            </InputGroup>
                            
                        </FormGroup>
                        <FormGroup>
                            <div className="custom-control custom-checkbox mb-2">
                                <input type="checkbox" className="custom-control-input" id="c1" />
                                <label className="custom-control-label" htmlFor="c1">–ó–∞–ø–æ–º–Ω–∏—Ç—å –ø–∞—Ä–æ–ª—å</label>
                            </div>
                        </FormGroup>
                        
                        <button type="submit" className="btn btn-success btn-block rounded-0 mt-3 font-weight-bold">
                            {loading ? <Loading color="#78dac3" classes="text-white" /> : '–í–æ–π—Ç–∏ –≤ —Ç–µ—Ä–º–∏–Ω–∞–ª'}
                        </button>
                    </form>
                    <p className="mt-3 mb-0">
                        <Link to="/reset" className="link">
                            <FaQuestionCircle className="mt-1" />&#160; –í–æ—Å—Å—Ç–∞–Ω–æ–≤–∏—Ç—å –ø–∞—Ä–æ–ª—å
                        </Link>
                    </p>
                </CardBody>
            </Card>

{/*
            <Tag tag="h2" data-type="slot" className="text-info display-1" style={{margin: '20px'}}>text</Tag>
            <ul>
                <li><b>Test:</b></li>
                <li>{i18n.translate('NotFound.heading')}</li>
                <li>{i18n.translate('NotFound.action')}</li>
                <li>{i18n.translate('NotFound.content')}</li>
                <li>{logined}</li>
            </ul> 
*/}
 
        </MainLayout>
    )
}